body{
    margin: 0;
    padding: 0;
    background-color: #ccc;
    }
    .welcome{
        font-size: 40px;
        font-family: 'Arvo', serif;
        position: absolute;
        top: 30%;
        left: 50%;
        -webkit-transform: translate(-50%, -50%);
            -ms-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
        margin: 0;
        padding: 0;
        display: -ms-flexbox;
        display: flex;
    }
    ul{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    margin: 0;
    padding: 0;
    display: -ms-flexbox;
    display: flex;
    
    }
    ul li{
    list-style: none;
    margin: 0 40px;
    
    }
    ul li .fa{
    font-size: 40px;
    color: #262626;
    line-height: 80px;
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s;
    
    }
    ul li a{
    position: relative;
    display: block;
    width: 80px;
    height: 80px;
    background-color: #fff;
    text-align: center;
    -webkit-transform: perspective(100px) rotate(-30deg) skew(25deg) translate(0,0);
            transform: perspective(100px) rotate(-30deg) skew(25deg) translate(0,0);
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s;
    -webkit-box-shadow: -20px 20px 10px rgb(0, 0, 0, 0.5);
            box-shadow: -20px 20px 10px rgb(0, 0, 0, 0.5);
    }
    ul li a::before{
    content: "";
    position: absolute;
    top: 10px;
    left: -20px;
    height: 100%;
    width: 20px;
    background: #b1b1b1;
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s;
    -webkit-transform: rotate(0deg) skewY(-45deg);
        -ms-transform: rotate(0deg) skewY(-45deg);
            transform: rotate(0deg) skewY(-45deg);
    }
    ul li a::after{
    content: "";
    position: absolute;
    top: 80px;
    left: -11px;
    height: 20px;
    width: 100%;
    background: #b1b1b1;
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s;
    -webkit-transform: rotate(0deg) skewX(-45deg);
        -ms-transform: rotate(0deg) skewX(-45deg);
            transform: rotate(0deg) skewX(-45deg);
    }
    ul li a:hover{
    -webkit-transform: perspective(1000px) rotate(-30deg) skew(25deg) translate(20px, -20px);
            transform: perspective(1000px) rotate(-30deg) skew(25deg) translate(20px, -20px);
    -webkit-box-shadow: -50px 50px 50px rgb(0, 0, 0, 0.5);
            box-shadow: -50px 50px 50px rgb(0, 0, 0, 0.5);
    }
    ul li:hover .fa{
    color: #fff;
    }
    ul li a:hover{
    -webkit-transform: perspective(1000px) rotate(-30deg) skew(25deg) translate(20px, -20px);
            transform: perspective(1000px) rotate(-30deg) skew(25deg) translate(20px, -20px);
    -webkit-box-shadow: -50px 50px 50px rgb(0, 0, 0, 0.5);
            box-shadow: -50px 50px 50px rgb(0, 0, 0, 0.5);
    }
    
    ul li:hover:nth-child(1) a{
    background: #dd4b39;
    }
    
    ul li:hover:nth-child(1) a:before{
    background: #c13929;
    }
    ul li:hover:nth-child(1) a:after{
    background: #e83322;
    }
    
  .container{max-width:1170px; margin:auto;}
  img{ max-width:100%;}
  .inbox_people {
    background: #f8f8f8 none repeat scroll 0 0;
    float: left;
    overflow: hidden;
    width: 40%; border-right:1px solid #c4c4c4;
  }
  .inbox_msg {
    background: #f8f8f8 none repeat scroll 0 0;
    border: 1px solid #c4c4c4;
    clear: both;
    overflow: hidden;
  }
  .top_spac{ margin: 20px 0 0;}
  
  
  .recent_heading {float: left; width:40%;}
  .srch_bar {
    display: inline-block;
    text-align: right;
    width: 60%;
  }
  .headind_srch{ padding:10px 29px 10px 20px; overflow:hidden; border-bottom:1px solid #c4c4c4;}
  
  .recent_heading h4 {
    color: #05728f;
    font-size: 21px;
    margin: auto;
  }
  .srch_bar input{ border:1px solid #cdcdcd; border-width:0 0 1px 0; width:80%; padding:2px 0 4px 6px; background:none;}
  .srch_bar .input-group-addon button {
    background: rgba(0, 0, 0, 0) none repeat scroll 0 0;
    border: medium none;
    padding: 0;
    color: #707070;
    font-size: 18px;
  }
  .srch_bar .input-group-addon { margin: 0 0 0 -27px;}
  
  .chat_ib h5{ font-size:15px; color:#464646; margin:0 0 8px 0;}
  .chat_ib h5 span{ font-size:13px; float:right;}
  .chat_ib p{ font-size:14px; color:#989898; margin:auto}
  .chat_img {
    float: left;
    width: 11%;
  }
  .chat_ib {
    float: left;
    padding: 0 0 0 15px;
    width: 88%;
  }
  
  .chat_people{ overflow:hidden; clear:both;}
  .chat_list {
    border-bottom: 1px solid #c4c4c4;
    margin: 0;
    padding: 18px 16px 10px;
  }
  .inbox_chat { height: 550px; overflow-y: scroll;}
  
  .active_chat{ background:#ebebeb;}
  
  .incoming_msg_img {
    display: inline-block;
    width: 6%;
  }
  .received_msg {
    display: inline-block;
    padding: 0 0 0 10px;
    vertical-align: top;
    width: 92%;
   }
   .received_withd_msg p {
    background: #ebebeb none repeat scroll 0 0;
    border-radius: 3px;
    color: #646464;
    font-size: 14px;
    margin: 0;
    padding: 5px 10px 5px 12px;
    width: 100%;
  }
  .time_date {
    color: #747474;
    display: block;
    font-size: 12px;
    margin: 8px 0 0;
  }
  .received_withd_msg { width: 57%;}
  .mesgs {
    float: left;
    padding: 30px 15px 0 25px;
    width: 60%;
  }
  
   .sent_msg p {
    background: #05728f none repeat scroll 0 0;
    border-radius: 3px;
    font-size: 14px;
    margin: 0; color:#fff;
    padding: 5px 10px 5px 12px;
    width:100%;
  }
  .outgoing_msg{ overflow:hidden; margin:26px 0 26px;}
  .sent_msg {
    float: right;
    width: 46%;
  }
  .input_msg_write input {
    background: rgba(0, 0, 0, 0) none repeat scroll 0 0;
    border: medium none;
    color: #4c4c4c;
    font-size: 15px;
    min-height: 48px;
    width: 100%;
  }
  
  .type_msg {border-top: 1px solid #c4c4c4;position: relative;}
  .msg_send_btn {
    background: #05728f none repeat scroll 0 0;
    border: medium none;
    border-radius: 50%;
    color: #fff;
    cursor: pointer;
    font-size: 17px;
    height: 33px;
    position: absolute;
    right: 0;
    top: 11px;
    width: 33px;
  }
  .messaging { padding: 0 0 50px 0;}
  .msg_history {
    height: 516px;
    overflow-y: auto;
  }
  .online {
    color: green;
  }
    
  .offline {
    color: red;
  }


